// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import * as iconset from './icon-assets.js'
import css from './ph-icon.css';

class PhIconSvgImg extends PhotonComponent {

    constructor(){
        super();
    }

    static get is():string {
        return 'ph-icon-svg-img';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            icon: PropTypes.string().nullable().attribute(),
            src: PropTypes.string().nullable().attribute(),
            size: PropTypes.number().nullable().attribute()
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        propRules.size.nullTo(24);
        this.useShadowDom = false;
        this.IDom = false;
        // propRules.icon.onChange(this.onIconChanged);
        // propRules.src.onChange(this.onSrcChanged);

    }

    getContent(self) {
        let props = this.props;
        if(props.icon) {
            return (
                <svg width = {props.size}
                     height = {props.size}
                     xmlns = "http://www.w3.org/2000/svg"
                     xmlnsXlink = "http://www.w3.org/1999/xlink"
                     viewBox="0 0 24 24"
                     preserveAspectRatio="xMidYMid meet"

                 >
                    {self.renderTagDangerously('g', {},  iconset[props.icon])}
                </svg>
           );
        }
        else if (props.src) {
            return (
              <img src={props.src} width={props.size} height={props.size}></img>
            );
        }
    }

    render(self:PhIconSvgImg):any {
      let props = self.props;
      let style = {width: props.size + 'px', height: props.size + 'px'};
      self.elem.style = style;
        return (
          <div style={style}>
              <style>{css.toString()}</style>
              {self.getContent(self)}
          </div>
        );
    }
}

definePhoton(PhIconSvgImg);



