// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import css from './ph-test.css';

export class PhTest extends PhotonComponent {

    static get is():string {
        return 'ph-test';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            bool: PropTypes.bool(),
            boolA: PropTypes.bool().attribute().reflect(),
            boolNA: PropTypes.bool().nullable().attribute().reflect(),

            num: PropTypes.number(),
            numA: PropTypes.number().attribute().reflect(),
            numNA: PropTypes.number().nullable().attribute().reflect(),

            str: PropTypes.string(),
            strA: PropTypes.string().attribute().reflect(),
            strNA: PropTypes.string().nullable().attribute().reflect()
        }
    }

    render(self:PhTest):any {
        let props:any = self.props;
        let formatVal:any = self.formatVal;
        return (
            <div>
                <style>{css.toString()}</style>
                <div>{'bool = ' + formatVal(props.bool)}</div>
                <div>{'boolA = ' + formatVal(props.boolA)}</div>
                <div>{'boolNA = ' + formatVal(props.boolNA)}</div>

                <div>{'num = ' + formatVal(props.num)}</div>
                <div>{'numA = ' + formatVal(props.numA)}</div>
                <div>{'numNA = ' + formatVal(props.numNA)}</div>

                <div>{'str = ' + formatVal(props.str)}</div>
                <div>{'strA = ' + formatVal(props.strA)}</div>
                <div>{'strNA = ' + formatVal(props.strNA)}</div>
            </div>
        );
    }

    formatVal(val) {
        switch (typeof val) {
            case "string":
                return "'" + val + "'";
            case "number":
            case "boolean":
                return val.toString();
            default:
                if (val === null) {
                    return 'null';
                }
                return typeof val;
        }
    }

}

definePhoton(PhTest);





