// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import css from './ph-image.css';

export class PhImage extends PhotonComponent {

    static get is():string {
        return 'ph-image';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            disabled: PropTypes.bool().attribute(),
            src: PropTypes.string().attribute(),
            alt: PropTypes.string().attribute(),
            sizing: PropTypes.string().attribute(),
            position: PropTypes.string().attribute(),
            preload: PropTypes.bool().attribute().reflect(),
            preventLoad: PropTypes.bool().attribute().reflect(),
            fade: PropTypes.bool().attribute().reflect(),
            placeholder: PropTypes.string().attribute(),
            width: PropTypes.number().attribute(),
            height: PropTypes.number().attribute()
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        //console.log(this.elem.tagName, 'PhImage.configurePropRules');
        propRules.src.onChange(this.onSrcChange);
    }

    onSrcChange = (newVal:string) => {
        //console.log('>>> onLabelsChange', newVal);
        this.reset();
        this.loading = !!newVal;
    };

    reset() {
        this.loading = false;
        this.loaded = false;
        this.error = false;
    }

    getSrc() {
        return this.props.preventLoad ? undefined : this.props.src;
    }

    render(self:PhImage):any {
        let props:any = self.props;
        // console.log('PhImage.render', props);
        return (
            <div>
                <style>{css.toString()}</style>
                <div id="sizedImgDiv"
                     style={
                         {  backgroundImage: self.getSrc() ? 'url("' + props.src + '")' : undefined,
                            backgroundSize: props.sizing,
                            backgroundPosition: props.sizing ? props.position : undefined,
                            backgroundRepeat: props.sizing ? 'no-repeat' : undefined
                         }
                     }
                     role="img"
                     hidden={self.computeImgDivHidden()}
                     aria-hidden={self.computeImgDivARIAHidden()}
                     aria-label={self.computeImgDivARIALabel()}>
                </div>
                <img id="img"
                     src={self.getSrc()}
                     alt={props.alt}
                     hidden={self.computeImgHidden()}
                     onload={self.onImgLoad}
                     onerror={self.onImgError}
                />
                <div id="placeholder"
                     hidden={self.computePlaceholderHidden()}
                     class={self.computePlaceholderClassName()}
                     style={{
                        backgroundImage: self.hasPlaceholder() ? 'url("' + self.getPlaceholder() + '")' : undefined,
                        backgroundSize: props.sizing,
                        backgroundPosition: props.sizing ? props.position : undefined,
                        backgroundRepeat: props.sizing ? 'no-repeat' : undefined
                     }}
                >
                </div>
            </div>
        );
    }

    onImgLoad = (e:Event) => {
        console.log('imgOnLoad', e);
        //if (e.imgTag.src !== this.src) return;
        this.loading = false;
        this.loaded = true;
        this.error = false;
        this.mustRender();
    };

    onImgError = (e:Event) => {
        console.log('imgOnError', e);
        // if (imgTag.src !== this.src) return;
        this.reset();
        this.loading = false;
        this.loaded = false;
        this.error = true;
        this.mustRender();
    };


    computePlaceholderHidden() {
        if (!this.props.preload || (!this.props.fade && !this.loading && this.loaded)) {
            return true;
        }
        else {
            return undefined;
        }
    }

    computePlaceholderClassName() {
        return (this.props.preload && this.props.fade && !this.loading && this.loaded) ? 'faded-out' : '';
    }

    computeImgDivHidden() {
        return !this.props.sizing;
    }

    computeImgDivARIAHidden() {
        return this.props.alt === '' ? 'true' : undefined;
    };

    computeImgDivARIALabel() {
        if (this.props.alt !== null) {
            return this.props.alt;
        }

        if (this.props.src === '') {
            return '';
        }
        //return something meaningful here.
    };

    computeImgHidden() {
        return !!this.props.sizing;
    }

    hasPlaceholder():boolean {
        return this.props.placeholder != null;
    }

    getPlaceholder():string {
        if (this.props.placeholder) {
            return this.props.placeholder;
        }
        return "data:image/gif;base64,";
    }

}

definePhoton(PhImage);



