// @Flow
import { define, vdom, prop } from 'skatejs';
import { emitCustomEvent } from 'photon-core';

import jss from './sk-tfu.jss';

function getLabel(elem:any, index:number):string {
    let result:string = '';
    let labels:string[] = elem.labels;
    if (labels && labels.length >= 2) {
        result = labels[index];
    }
    //console.log('getLabel', index, result);
    if (!result) {
        result = '?labels missing?';
        //result = index ? 'No' : 'Yes';
    }
    return result;
}

function onTrueClick(e:any) {
    console.log('onTrueClick', e);
    e.preventDefault();
    // this.elem.value = (this.elem.value === true) ? null : true;
    let newVal = (this.value === 'true') ? null : 'true';
    emitCustomEvent(this, 'change', {detail:newVal});
}

function onFalseClick(e:any) {
    console.log('onFalseClick', e);
    e.preventDefault();
    // this.elem.value = (this.elem.value === false) ? null : false;
    let newVal = (this.value === 'false') ? null : 'false';
    emitCustomEvent(this, 'change', {detail:newVal});
}

// //example of public method
// cycle(a, b) {
//     console.log('public method cycle', 'a:', a, 'b:',  b);
//     if (this.props.value === true) {
//         this.elem.value = false;
//     }
//     else if (this.props.value === false){
//         this.elem.value = null;
//     }
//     else if (this.props.value === null){
//         this.elem.value = true;
//     }
// }



export default define('sk-tfu', {
    props: {
        value: prop.string({
            attribute: true
        }),

        labels: prop.array({
            attribute: true,
            default: ['YYYY', 'NNN']

        }),
    },

    created (elem) {
        elem.cycle = (a, b) => {
            console.log('public method cycle', 'a:', a, 'b:',  b);
            if (elem.value === 'true') {
                elem.value = 'false';
            }
            else if (elem.value === 'false'){
                elem.value = null;
            }
            else if (elem.value === null){
                elem.value = 'true';
            }
        }

    },

    render: (elem) => {
        // let a = "<b>InnerHTML</b>";
                // <span>{self.renderTagDangerously('div', {aa:123, bb:'test'}, a)}</span>
        return (
            <div
                style={jss['.container']}
            >
                <slot name="a"/>
                <span
                    style={elem.value === 'true' ? jss['.selected'] : null}
                    onclick={onTrueClick.bind(elem)}
                >{getLabel(elem, 0)}</span>
                <slot name="k"/>
                <span
                    style={elem.value === 'false' ? jss['.selected'] : null}
                    onclick={onFalseClick.bind(elem)}
                >{getLabel(elem, 1)}</span>
                <slot name="r"/>
            </div>
        );

    },


});



