// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import css from './ph-icon-badge.css';

export class PhIconBadge extends PhotonComponent {

    static get is():string {
        return 'ph-icon-badge';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            bordered: PropTypes.bool().attribute(),
            icon: PropTypes.string().attribute(),
            status: PropTypes.string().nullable().attribute().reflect(),
            size: PropTypes.string().nullable().attribute().reflect()
        }
    }

    initialize() {
        super.initialize();
        this.sizes = {
            'small': 10,
            'medium': 14,
            'large': 18
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        propRules.bordered.nullTo('false');
        propRules.size.nullTo('large');
        propRules.status.nullTo('primary');
    }

    getContent() {
        let props = this.props;
        let size = this.sizes[props.size];
        let styles = {
            fontSize: '0',
            lineHeight: '0'
        }
        return <ph-icon name={props.icon} size={size} style={styles}></ph-icon>
    }

    render(self:PhIconBadge):any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {self.getContent()}
            </div>
        );
    }
}

definePhoton(PhIconBadge);




