// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import css from './ph-checkbox.css';

export class PhCheckbox extends PhotonComponent {

    static get is():string {
        return 'ph-checkbox';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            label: PropTypes.string().nullable().attribute(),
            checked: PropTypes.bool().attribute().reflect('aria-checked'),
            disabled: PropTypes.bool().attribute().reflect('aria-disabled'),
            reverse: PropTypes.bool().attribute()
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        console.log(this.elem.tagName, 'PhCheckbox.configurePropRules');
        propRules.label.nullTo('check me');
        propRules.disabled.onChange(this.onDisableChange);
    }

    prevTabIndex:number;
    onDisableChange = (newVal:boolean) => {
        //console.log('>>> onDisableChange', newVal, this.elem.tabIndex);
        if (newVal) {
            this.prevTabIndex = this.elem.tabIndex;
            this.elem.removeAttribute('tabindex');
        }
        else {
            this.elem.tabIndex = this.prevTabIndex;
        }
    };

    initialize() {
        super.initialize();
        console.log(this.elem.tagName, 'PhCheckbox.initialize');
    }

    connectedCallback() {
        super.connectedCallback();
        this.elem.addEventListener('click', this.handleClickCapture, true);
    }

    disconnectedCallback() {
        this.elem.removeEventListener('click', this.handleClickCapture, true);
        super.disconnectedCallback();
    }

    handleClick = (e:Event) => {
        //console.log('handleClick', e, e.defaultPrevented);
        if (this.props.disabled) {
            e.stopPropagation();
            return;
        }
        this.elem.checked = !this.props.checked;
    };

    handleClickCapture = (e:Event) => {
        //console.log('handleClickResult', e, e.defaultPrevented);
        if (!e.defaultPrevented) {
            window.setTimeout(() => {
                this.elem.dispatchEvent(new Event('change'));
            });
        }
    };

    render(self:PhCheckbox):any {
        let props:any = self.props;
        return (
            <span id="container" onclick={self.handleClick}>
                <style>{css.toString()}</style>
                <span id="box">
                    <svg id="check" width="16" height="16" viewBox="0 0 16 16">
                        <path d="M16,5.72a0.84,0.84,0,0,1-.25.62L8,14.12a0.87,0.87,0,0,1-1.23,0L2.24,9.62a0.87,0.87,0,0,1,0-1.23L3.47,7.15a0.87,0.87,0,0,1,1.23,0L7.36,9.82,13.3,3.88a0.87,0.87,0,0,1,1.23,0l1.23,1.23A0.84,0.84,0,0,1,16,5.72Z"/>
                    </svg>
                </span>
                <span id="label">{props.label}</span>
            </span>
        );
    }
}

definePhoton(PhCheckbox);



