// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import * as icons from './icon-assets.js'
import css from './ph-icon.css';

class PhIcon extends PhotonComponent {

    constructor(){
        super();
        //this.useIDom = false;
        //this.useShadowDom = false;
    }

    static get is():string {
        return 'ph-icon';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            name: PropTypes.string().nullable().attribute(),
            size: PropTypes.number().nullable().attribute()
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        propRules.name.nullTo('usd');
        propRules.size.nullTo(24);
    }
    
    render(self:PhIcon):any {
        //self.elem.innerHTML = '<svg viewbox="0 0 24 24" width="'+self.props.size+'" height="'+self.props.size+'">' + icons[self.props.name] + '</svg>';
        return self.renderTagDangerously('svg',
            {viewBox:'0 0 24 24',
                width: self.props.size,
                height: self.props.size,
            },
            icons[self.props.name]);
    }
}

definePhoton(PhIcon);



