// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import jss from './ph-button.jss';

export class PhButton extends PhotonComponent {

    static get is():string {
        return 'ph-button';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            value: PropTypes.bool().nullable().attribute().reflect('aria-value'),
            labels: PropTypes.arrayOf('string').attribute().reflect('debug-labels')
        }
    }

    constructor(){
        super();
        console.log('PhButton.constructor');
        // this.useIDom = false;
        // this.useShadowDom = false;
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        //console.log(this.elem.tagName, 'PhTfu.configurePropRules');
        propRules.labels.nullTo(['YYYY', 'NNN']);
    }


    getLabel(index:number):string {
        let result:string = '';
        // let labels:string[] = this.elem.labels;
        let labels:string[] = this.props.labels;
        if (labels && labels.length >= 2) {
            result = labels[index];
        }
        //console.log('getLabel', index, result);
        if (!result) {
            result = '?labels missing?';
            //result = index ? 'No' : 'Yes';
        }
        return result;
    }

    onTrueClick = (e:any) => {
        // console.log('onTrueClick', e);
        e.preventDefault();
        // this.elem.value = (this.elem.value === true) ? null : true;
        let newVal = (this.props.value === true) ? null : true;
        this.emit('change', newVal);
    };

    onFalseClick = (e:any) => {
        // console.log('onFalseClick', e);
        e.preventDefault();
        // this.elem.value = (this.elem.value === false) ? null : false;
        let newVal = (this.props.value === false) ? null : false;
        this.emit('change', newVal);
    };

    //example of public method
    cycle(a, b) {
        console.log('public method cycle', 'a:', a, 'b:',  b);
        if (this.props.value === true) {
            this.elem.value = false;
        }
        else if (this.props.value === false){
            this.elem.value = null;
        }
        else if (this.props.value === null){
            this.elem.value = true;
        }
    }

    render(self:PhButton):any {
        let props:any = self.props;
        console.log('PhButton.render', props);

        return (
            <div
                style={jss['.container']}
            >
                <slot name="a"/>
                <span
                    style={props.value === true ? jss['.selected'] : null}
                    onclick={self.onTrueClick}
                >{self.getLabel(0)}</span>
                <slot name="k"/>
                <span
                    style={props.value === false ? jss['.selected'] : null}
                    onclick={self.onFalseClick}
                >{self.getLabel(1)}</span>
                <slot name="r"/>
            </div>
        );
    }

}


definePhoton(PhButton, HTMLButtonElement, 'button');




