// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import css from './ph-avatar.css';

export class PhAvatar extends PhotonComponent {

    static get is():string {
        return 'ph-avatar';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            image: PropTypes.string().nullable().attribute(),
            initials: PropTypes.string().attribute(),
            size: PropTypes.string().nullable().attribute().reflect(),
            icon: PropTypes.string().attribute()
        }
    }

    initialize() {
        super.initialize();
        this.sizes = {
            'small': 15,
            'medium': 25,
            'large': 35
        }
    }

    configurePropRules(propRules:{[propName:string]:PropRule}) {
        propRules.size.nullTo('medium');
    }

    getContent() {
        let props = this.props;
        if(props.image) {
            return <ph-image src={props.image} sizing="cover"></ph-image>
        }
        else if(props.icon) {
            let size = this.sizes[props.size];
            let styles = {
                fontSize: '0'
            };
            return <ph-icon name={props.icon} size={size} style={styles}></ph-icon>
        }
        else if(props.initials) {
            return <span>{props.initials}</span>
        }
    }

    render(self:PhAvatar):any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {self.getContent()}
            </div>
        );
    }
}

definePhoton(PhAvatar);




