// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
} from 'photon-core';

import {PhAvatar} from './../ph-avatar/ph-avatar';

import css from './ph-user-avatar.css';

export class PhUserAvatar extends PhAvatar {

    static get is():string {
        return 'ph-user-avatar';
    }

    static get propTypes():{[k:string]:PropType} {
        return {
            imageUrl: PropTypes.string().nullable().attribute(),
            initials: PropTypes.string().nullable().attribute(),
            isOrganization: PropTypes.bool().nullable().attribute(),
            size: PropTypes.string().nullable().attribute().reflect()
        }
    }

    getContent() {
        let props = this.props;
        if(props.isOrganization) {
            let size = this.sizes[props.size];
            let styles = {
                fontSize: '0'
            };
            return <ph-icon name='building' size={size} style={styles}></ph-icon>
        } 
        else if(props.imageUrl) {
            return <ph-image src={props.imageUrl} sizing="cover"></ph-image>
        }
        else if(props.initials) {
            return <span>{props.initials}</span>
        }
    }

    render(self:PhUserAvatar):any {
        return (
            <div id="container">
                <style>{css.toString()}</style>
                {self.getContent()}
            </div>
        );
    }
}

definePhoton(PhUserAvatar);




