// @Flow

import {
    vdom,
    iDOMHelpers,
    PhotonComponent,
    definePhoton,
    PropTypes,
    PropType,
    noValue
} from 'photon-core';

import css from './ph-test-tester.css';

const propValues = [undefined, null, NaN, '', '0', '1', 'false', 'true', 0, 1, false, true];
const attrValues = [undefined, null, 'NaN', '', '0', '1', 'false', 'true'];

export class PhTestTester extends PhotonComponent {

    static get is():string {
        return 'ph-test-tester';
    }

    constructor() {
        super();
    }

    bool:boolean = null;
    boolA:boolean = false;
    boolNA:any = null;

    num:number = null;
    numA:number = -9;
    numNA:number = null;

    str:string = null;
    strA:string = 'Hi';
    strNA:string = null;

    get phTest():any {
        return this.getElementByTagName('ph-test');
    }

    render(self:PhTestTester):any {

        return (
            <div>
                <style>{css.toString()}</style>

                <br/>
                <ph-test name="phTest"
                         bool={self.bool}
                         bool-a={self.boolA}
                         bool-n-a={self.boolNA}

                         num={self.num}
                         num-a={self.numA}
                         num-n-a={self.numNA}

                         str={self.str}
                         str-a={self.strA}
                         str-n-a={self.strNA}
                ></ph-test>

                <br/>
                {self.renderTestProp(self, 'bool')}
                {self.renderTestProp(self, 'boolA')}
                {self.renderTestAttr(self, 'bool-a')}
                {self.renderTestProp(self, 'boolNA')}
                {self.renderTestAttr(self, 'bool-n-a')}

                <br/>
                {self.renderTestProp(self, 'num')}
                {self.renderTestProp(self, 'numA')}
                {self.renderTestAttr(self, 'num-a')}
                {self.renderTestProp(self, 'numNA')}
                {self.renderTestAttr(self, 'num-n-a')}

                <br/>
                {self.renderTestProp(self, 'str')}
                {self.renderTestProp(self, 'strA')}
                {self.renderTestAttr(self, 'str-a')}
                {self.renderTestProp(self, 'strNA')}
                {self.renderTestAttr(self, 'str-n-a')}


            </div>
        );
    }

    renderTestProp(self, propName) {
        return (
            <div>
                {propName + ' '}
                {propValues.map((val) => {
                    return <button onclick={self.setPropVal.bind(self, propName, val)}>{self.formatVal(val)}</button>;
                })}
                {' ' + self.printProp(propName)}
            </div>
        )
    }

    renderTestAttr(self, attrName) {
        return (
            <div>
                {attrName + ' '}
                {attrValues.map((val) => {
                    return <button onclick={self.setAttrVal.bind(self, attrName, val)}>{self.formatVal(val)}</button>;
                })}
                {' ' + self.printAttr(attrName)}
            </div>
        )
    }

    setPropVal(propName:string, val:any) {
        let phTest:any = this.phTest;
        phTest[propName] = val;
        this.mustRender();
    }

    setAttrVal(attrName:string, val:string) {
        let phTest:any = this.phTest;
        if (noValue(val)) {
            phTest.removeAttribute(attrName);
        }
        else {
            phTest.setAttribute(attrName, val);
        }
        this.mustRender();
    }


    printProp(propName:string) {
        let phTest:any = this.phTest;
        let val:any = phTest ? phTest[propName] : undefined;
        return this.formatVal(val);
    }

    printAttr(attrName:string) {
        let phTest:any = this.phTest;
        let val:any = phTest ? phTest.getAttribute(attrName) : undefined;
        return this.formatVal(val);
    }

    formatVal(val:any):string {
        switch (typeof val) {
            case 'string':
                return '\'' + val + '\'';
            case 'number':
            case 'boolean':
                return val.toString();
            default:
                if (val === null) {
                    return 'null';
                }
                return typeof val;
        }
    }

}

definePhoton(PhTestTester);





